.Card {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 250px;
  margin: 20px;
  text-align: center;
  img {
    width: auto;
  }
  h1, p{
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    color: #bfb7b7;
  }
}